export default {
  name: "reviews",
  props: {
    shopId: {
      type: String
    },
    shopTil: {
      type: String
    },
    //总页数
    countNumber: {
      type: Number
    },
    comment_rating: {
      type: Number
    }
  },
  data() {
    return {
      setStar: 0,
      hasReviews: false,
      reviewsData: [],
      starColor: ["#FFD282",'#FFD282','#FFD282'],
      page: 4,//每页显示条数
      wordColor:{
          "a":"#586695",
          "b":"#562501",
          "c":"#661713",
          "d":"#694639",
          "e":"#255933",
          "f":"#592029",
          "g":"#601042",
          "h":"#500344",
          "i":"#671989",
          "j":"#632914",
          "k":"#165260",
          "l":"#672492",
          "m":"#714150",
          "n":"#440202",
          "o":"#319072",
          "p":"#756713",
          "q":"#249542",
          "r":"#207815",
          "s":"#714330",
          "t":"#027515",
          "u":"#791505",
          "v":"#045901",
          "w":"#898157",
          "x":"#255773",
          "y":"#255933",
          "z":"#017952"
      }
    };
  },
  mounted() {
    this.handleCurrentChange(1);
  },
  methods: {
    handleCurrentChange(val) {
      this.$apis
        .getReviews(
          {
            product_id: this.shopId,
            page: val,
            per_page: this.page
          },
          {}
        )
        .then(res => {
          console.log(res);
          if (res.status == 1) {
            if (res.data.length != 0) {
              this.hasReviews = true;
            }
            this.reviewsData = res.data.items;
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  filters: {
    conversionTime: function(time) { //author: meizz
      let fmt = "MM/dd/yyyy"
      const date = new Date(time * 1000)
      const o = {
        "M+": date.getMonth() + 1, //月份
        "d+": date.getDate(), //日
        "h+": date.getHours(), //小时
        "m+": date.getMinutes(), //分
        "s+": date.getSeconds(), //秒
        "q+": Math.floor((date.getMonth() + 3) / 3), //季度
        "S": date.getMilliseconds() //毫秒
      };
      if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length));
      for (let k in o)
        if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
      return fmt;
    },
    cutStr(str) {
      let firstStr = str.slice(0, 1);
      let lastStr = str.slice(str.length - 1, str.length);
      return firstStr + "***" + lastStr;
    },
    cutStr2(str) {
      let firstStr = str.slice(0, 20);
      return firstStr + "...";
    },
    cutStr3(str) {
      return str.slice(0, 1).toLowerCase();
    },
    cutStr4(str) {
      return str.slice(0, 1);
    },
  }
};
